import React from 'react';

const Precio = (props) => {
    return (        
        <div className="card text-white bg-success my-5">
            <div className="card-header">Precio del Bitcoin</div>
            <div className="card-body">
                <h4 className="card-title">Precio Actual:</h4>
                <div className="list-group-item d-flex justify-content-between align-items-center bg-success">
                    <p className="card-text">Última Hora: </p>
                    <p className="card-text">Últimas 24 Horas: </p>
                    <p className="card-text">Últimos 7 días: </p>
                </div>
                
            </div>            
        </div>       
    );
};

export default Precio;